import interfaceurl from '@/utils/interface.js'
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	props: {
		show: {
		  type: Boolean
		},
		//显示用户信息或手机
		showUserOrPhone: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			token: '' //先用存储本地的token判断
		};
	},
	//computed: mapState(['token', 'userInfo']),
	mounted() {
		this.token = uni.getStorageSync('token');
	},
	methods: {
		prevent() { }, //阻止父级元素跳转
		bindGetUserInfo(res){
			console.log(res)
			if(res.detail.rawData){
				interfaceurl.login(res.detail);
			} else {
				uni.showModal({
				    showCancel: false,
				    content: '授权失败'
				});
			}
		},
		getPhoneNumber(res) {
		    console.log(res);
			if (res.detail.encryptedData) {
				let params = {
					sessionKey: this.userInfo.sessionKey,
					openId: this.userInfo.openId,
					rawData: this.userInfo.rawData,
					signature: this.userInfo.signature,
					encryptedData: res.detail.encryptedData,
					iv: res.detail.iv
				}
				let that = this;
				interfaceurl.checkAuth(interfaceurl.getMobile, params).then((res) => {
					that.userInfo.mobile = res.data.mobile;
					that.$store.commit('setUserInfo', that.userInfo);
					uni.setStorageSync('userInfo', that.userInfo);
				});
			} else {
				uni.showModal({
				    showCancel: false,
				    content: '您取消了授权，请允许授权，以便提供更好的服务'
				});
			}
		}
	}
}
